<template>
      <div>
        <canvas id="subtheme-chart"></canvas>
      </div>
</template>

<script>
import Chart from 'chart.js/auto';
import * as aq from 'arquero';

export default {
    name:'SubThemeChart',
    data() {
        this.chart = null
        return {

        }
    },
    computed: {
        actions() {
            return this.$store.state.filteredData
        },
        countPerSubTheme() {
            let count = aq.from(this.actions)
                .groupby('sous_theme')
                .count()
                .orderby('sous_theme')
                .objects();
            return count
        },
        filterTheme() {
            return this.$store.state.filterKey
        },
        filterCode() {
            return this.$store.state.filterCode
        }
    },
    watch: {
        filterTheme() {

            // !! DEUX mthodes pour mettre  jour le graphique
            // 1re mthode : supprimer et regnrer le graphique
            // this.chart.destroy()
            // this.createChart()

            // 2e mthode : mettre  jour en injecter les donnes actualises
            let dataset = this.countPerSubTheme.map(e => e.count );
            let labels = this.countPerSubTheme.map(e => e.sous_theme );
            this.chart.data.datasets[0].data = dataset;
            this.chart.data.labels = labels;
            this.chart.update()
        },
        filterCode() {
            // mme code qu'au dessus
            let dataset = this.countPerSubTheme.map(e => e.count );
            let labels = this.countPerSubTheme.map(e => e.sous_theme );
            this.chart.data.datasets[0].data = dataset;
            this.chart.data.labels = labels;
            this.chart.update()
        }
    },
    mounted() {
        this.createChart()
    },
    methods: {
        createChart() {
            let labels = this.countPerSubTheme.map(e => {
                return e.sous_theme
            });
            
            let dataset = this.countPerSubTheme.map(e => {
                return e.count
            });
            console.log(dataset);

            const ctx = document.getElementById('subtheme-chart');
            let chartOptions = {
                type: 'bar',   // le type du graphique
                data:{
                labels: labels,
                datasets:[{
                    data:dataset,
                    labels:labels,
                    backgroundColor: ['#FF6633', '#FFB399', '#FF33FF', '#FFFF99', '#00B3E6', 
                                    '#E6B333', '#3366E6', '#999966', '#99FF99', '#B34D4D',
                                    '#80B300', '#809900', '#E6B3B3', '#6680B3', '#66991A', 
                                    '#FF99E6', '#CCFF1A', '#FF1A66', '#E6331A', '#33FFCC',
                                    '#66994D', '#B366CC', '#4D8000', '#B33300', '#CC80CC', 
                                    '#66664D', '#991AFF', '#E666FF', '#4DB3FF', '#1AB399',
                                    '#E666B3', '#33991A', '#CC9999', '#B3B31A', '#00E680', 
                                    '#4D8066', '#809980', '#E6FF80', '#1AFF33', '#999933',
                                    '#FF3380', '#CCCC00', '#66E64D', '#4D80CC', '#9900B3', 
                                    '#E64D66', '#4DB380', '#FF4D4D', '#99E6E6', '#6666FF'],
                    // backgroundColor: this.getbgColors(),
                    hoverOffset: 5,
                }]
                },
                options:{
                    plugins: {
                        legend: {
                            display: false,
                            position:'right',
                            direction:'horizontal',
                            align: "start",  // alignement  gauche 
                            labels:{
                                usePointStyle:true,
                            },
                            onClick: null, // dsactive le filtre au clic sur un item de lgende
                        },
                        title: {
                            display:false,
                            text:'coucou',
                            options: {
                            fullSize:false
                            }
                        },
                        tooltip: {
                            position: 'nearest',
                        }
                    },
                    layout: {
                        padding:20
                    },
                    responsive: true,
                    maintainAspectRatio: false
                }
            };
            
            // initialisation sur la page
            let chart = new Chart(ctx, chartOptions);
            this.chart = chart;
        },        
    },

}
</script>

<style scoped>
    canvas {
        height: 300px;
    }


</style>


